html,
body {
  margin: 0;
  padding: 0;
  background-color: #111111;
}

#wrapper {
  height: 100vh;
  width: 46vh;
  position: relative;
}

#mainCanvas {
  height: 100%;
  width: 100%;
}

#root {
  text-align: center;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#info {
  position: absolute;
  top: 1em;
  left: 1em;
  color: white;
  font-weight: bold;
}
